  color: #f6f6ef;
  font-family: poppins; }

.flex {
  display: flex;
  align-items: center; }

.btn {
  background-color: #61cdff;
  padding: .5rem;
  padding-inline: .8rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer; }

header {
  background-color: #17171c;
  padding-inline: 1rem;
  height: 70px; }
  header .logo-section {
    padding-inline: .5rem;
    gap: .5rem;
    border-right: 1px solid rgba(255, 255, 255, 0.404); }
    header .logo-section .logo-box {
      height: 30px;
      width: 30px;
      background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
      background-size: cover; }
  header nav {
    justify-content: space-between;
    width: 100%;
    padding-inline-start: .7rem; }
    header nav .left-nav {
      gap: clamp(0.5rem, 1.5vw, 2rem); }
      header nav .left-nav .store {
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: 600; }
      header nav .left-nav .support,
      header nav .left-nav .distribute {
        opacity: .7; }
    header nav .right-nav {
      gap: clamp(0.5rem, 1vw, 1rem); }
      header nav .right-nav .user {
        height: 40px;
        aspect-ratio: 1;
        background-color: #313135;
        border-radius: 100vh;
        justify-content: center; }

main {
  background-color: #121212; }
